/* src/App.css */

.App {
  font-family: Arial, sans-serif;
  padding: 20px;
  color: #333;
  max-width: 1000px;
  margin: 0 auto;
  background: linear-gradient(135deg, #f0f0f0, #d0e1ff);
  animation: gradient 10s ease infinite;
}

/* Profile Section */
.App-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.profile-text h1 {
  font-size: 18px;
  color: #888;
  margin: 0;
}

.profile-text h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

/* Animated Background Gradient */
@keyframes gradient {
  0% { background-color: #f0f0f0; }
  50% { background-color: #d0e1ff; }
  100% { background-color: #f0f0f0; }
}

/* About Statement */
.about-statement {
  margin: 40px 0;
}

.about-statement h3 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.about-statement p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
}

/* Project Cards with Flip Animation */
.projects-section {
  margin-top: 40px;
}

.projects-section h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.project-card {
  perspective: 1000px;
  margin-bottom: 20px;
}

.project-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.project-card:hover .project-card-inner {
  transform: rotateY(180deg);
}

.project-card-front, .project-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.project-card-back {
  transform: rotateY(180deg);
  background-color: #f8f8f8;
  color: #333;
  padding: 20px;
}

.project-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}
